@import '../../common/styles/sass/mixins/mixins';
@import '../../common/styles/sass/variables';

.work {
  width: 250px;
  background-color: #434d56;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .image {
    height: 300px;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.2;
    }
  }

  h3 {
    @include text(('Poppins', cursive), 20px, $brightTextColor)
  }
span{
  @include text(('Poppins', cursive), 15px, $brightTextColor)
}
  .description {
    //background-color: ;
  }

}





